/* Calendar Container */
.calendar-container {
    font-family: Arial, sans-serif;
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    margin: 0 auto;
    padding: 10px;
    text-align: center;
  }
  
  /* Calendar Header */
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .header button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  
  /* Calendar Month and Year */
  .header h2 {
    margin: 0;
  }
  
  /* Calendar Days */
  .days {
    display: flex;
    justify-content: space-between;
    background-color: #f0f0f0;
    padding: 5px 0;
  }
  
  .day {
    width: calc(100% / 7);
    text-align: center;
    font-weight: bold;
  }
  
  /* Calendar Body */
  .body {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 5px;
  }
  
  .cell {
    width: calc(100% / 7);
    padding: 10px;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .cell:hover {
    background-color: #f0f0f0;
  }
  
  /* Disabled Cells (for days outside the current month) */
  .disabled {
    color: #ccc;
    pointer-events: none;
    cursor: default;
  }
  